//==JSLodopWebCLodop/Lodop7==
import { MessageBox } from 'element-ui'
//JSLodop.js(CLodopfuncs.js):

function setJsUrl(remoteIp = 'localhost') {
  var MainJS ="CLodopfuncs.js"
  window.URL_WS1   = "ws://" + remoteIp + ":8000/"+MainJS,                //ws8000/18000
  window.URL_WS2   = "ws://" + remoteIp + ":18000/"+MainJS,                //ws8000/18000
  window.URL_HTTP1 = "http://" + remoteIp + ":8000/"+MainJS,              //http8000/18000
  window.URL_HTTP2 = "http://" + remoteIp + ":18000/"+MainJS,
  window.URL_HTTP3 = "https://" + remoteIp +  ".lodop.net:8443/"+MainJS;   //https8000/8443
}
var CreatedOKLodopObject, CLodopIsLocal, LoadJsState;

//==CLodop():==
function needCLodop() {
    try {
        var ua = navigator.userAgent;
        if (ua.match(/Windows\sPhone/i) ||
            ua.match(/iPhone|iPod|iPad/i) ||
            ua.match(/Android/i) ||
            ua.match(/Edge\D?\d+/i))
            return true;
        var verTrident = ua.match(/Trident\D?\d+/i);
        var verIE = ua.match(/MSIE\D?\d+/i);
        var verOPR = ua.match(/OPR\D?\d+/i);
        var verFF = ua.match(/Firefox\D?\d+/i);
        var x64 = ua.match(/x64/i);
        if ((!verTrident) && (!verIE) && (x64)) return true;
        else if (verFF) {
            verFF = verFF[0].match(/\d+/);
            if ((verFF[0] >= 41) || (x64)) return true;
        } else if (verOPR) {
            verOPR = verOPR[0].match(/\d+/);
            if (verOPR[0] >= 32) return true;
        } else if ((!verTrident) && (!verIE)) {
            var verChrome = ua.match(/Chrome\D?\d+/i);
            if (verChrome) {
                verChrome = verChrome[0].match(/\d+/);
                if (verChrome[0] >= 41) return true;
            }
        }
        return false;
    } catch (err) {
        return true;
    }
}

//==http(s)==
//==CLODOP6.561/Lodop7.043)==
function checkOrTryHttp() {
  if (window.getCLodop) {
     LoadJsState = "complete";
     return true;
  }
  if (LoadJsState == "loadingB" || LoadJsState == "complete") return;
  LoadJsState = "loadingB";
  var head = document.head || document.getElementsByTagName("head")[0] || document.documentElement;
  var JS1 = document.createElement("script")
     ,JS2 = document.createElement("script")
     ,JS3 = document.createElement("script");
  JS1.src = URL_HTTP1;
  JS1.id = 'lodop';
  JS2.src = URL_HTTP2;
  JS2.id = 'lodop';
  JS3.src = URL_HTTP3;
  JS3.id = 'lodop';
  JS1.onload = JS2.onload = JS3.onload = JS2.onerror = JS3.onerror=function(){
    LoadJsState = "complete"
  }
  JS1.onerror = function(e) {
      if (window.location.protocol !== 'https:')
          head.insertBefore(JS2, head.firstChild); else
          head.insertBefore(JS3, head.firstChild);
  }
  head.insertBefore(JS1,head.firstChild);
}
function loadCLodop(){
  if (!needCLodop()) return;
  CLodopIsLocal = !!((URL_WS1 + URL_WS2).match(/\/\/localho|\/\/127.0.0./i));
  LoadJsState = "loadingA";
  if (!window.WebSocket && window.MozWebSocket) window.WebSocket=window.MozWebSocket;
  //ws(200ms)CORS,Lodop:
  try {
    var WSK1=new WebSocket(URL_WS1);
    WSK1.onopen = function(e) { setTimeout(checkOrTryHttp(),200); }
    WSK1.onmessage = function(e) {if (!window.getCLodop) eval(e.data);}
    WSK1.onerror = function(e) {
         var WSK2=new WebSocket(URL_WS2);
         WSK2.onopen = function(e) {setTimeout(checkOrTryHttp(),200);}
         WSK2.onmessage = function(e) {if (!window.getCLodop) eval(e.data);}
         WSK2.onerror= function(e) {checkOrTryHttp();}
    }
  } catch(e){
    checkOrTryHttp();
  }
}
//==Lodop:==
(function(){ setJsUrl(); loadCLodop() })()

//==LODOP,:==
function getLodop2(oOBJECT, oEMBED) {
    var strFontTag = "<br><font color='#FF00FF'>";
    var strLodopInstall = strFontTag + "!<a href='install_lodop32.exe' target='_self'></a>";
    var strLodopUpdate = strFontTag + "!<a href='install_lodop32.exe' target='_self'></a>";
    var strLodop64Install = strFontTag + "!<a href='install_lodop64.exe' target='_self'></a>";
    var strLodop64Update = strFontTag + "!<a href='install_lodop64.exe' target='_self'></a>";
    var strCLodopInstallA = "<font color='#FF00FF'>WebCLodop<a href='https://www.lodop.net/download/CLodop_Setup_for_Win64NT_6.577EN.zip' target='_self'></a>";
    var strCLodopInstallB = "<br><a href='CLodop.protocol:setup' target='_self'></a>";
    var strCLodopUpdate = "<br><font color='#FF00FF'>WebCLodop!<a href='CLodop_Setup_for_Win32NT.exe' target='_self'></a>";
    var strLodop7FontTag = "<br><font color='#FF00FF'>WebLodop7";
    var strLodop7HrefX86 = "<a href='Lodop7_Linux_X86_64.tar.gz' target='_self'></a>(lodop)";
    var strLodop7HrefARM = "<a href='Lodop7_Linux_ARM64.tar.gz'  target='_self'></a>(lodop)";
    var strLodop7Install_X86 = strLodop7FontTag + "" + strLodop7HrefX86;
    var strLodop7Install_ARM = strLodop7FontTag + "" + strLodop7HrefARM;
    var strLodop7Update_X86 = strLodop7FontTag + "" + strLodop7HrefX86;
    var strLodop7Update_ARM = strLodop7FontTag + "" + strLodop7HrefARM;
    var strInstallOK = "</font>";
    var LODOP;
    try {
        var isWinIE = (/MSIE/i.test(navigator.userAgent)) || (/Trident/i.test(navigator.userAgent));
        var isWinIE64 = isWinIE && (/x64/i.test(navigator.userAgent));
        var isLinuxX86 = (/Linux/i.test(navigator.platform)) && (/x86/i.test(navigator.platform));
        var isLinuxARM = (/Linux/i.test(navigator.platform)) && (/aarch/i.test(navigator.platform));

        if (needCLodop() || isLinuxX86 || isLinuxARM) {
            try {
                LODOP = window.getCLodop();
            } catch (err) {}
            if (!LODOP && LoadJsState !== "complete") {
                if (!LoadJsState) {
                  // alert("LodopJSloadCLodop.")
                  MessageBox.alert('LodopJSloadCLodop', '', { type: 'error' })
                } else {
                  // alert(".");
                  MessageBox.alert('', '', { type: 'error' })
                }
                return;
            }
            var strAlertMessage;
            if (!LODOP) {
                if (isLinuxX86)
                    strAlertMessage = strLodop7Install_X86;
                else if (isLinuxARM)
                    strAlertMessage = strLodop7Install_ARM;
                else
                    strAlertMessage = strCLodopInstallA + (CLodopIsLocal ? strCLodopInstallB : "");
                    MessageBox.alert(strAlertMessage + strInstallOK, '', { dangerouslyUseHTMLString: true })
                // document.body.innerHTML = strAlertMessage + strInstallOK + document.body.innerHTML;
                return;
            } else {
                if (isLinuxX86 && LODOP.CVERSION < "7.0.4.3")
                    strAlertMessage = strLodop7Update_X86;
                else if (isLinuxARM && LODOP.CVERSION < "7.0.4.3")
                    strAlertMessage = strLodop7Update_ARM;
                else if (CLODOP.CVERSION < "6.5.7.7")
                    strAlertMessage = strCLodopUpdate;

                if (strAlertMessage) {
                  MessageBox.alert(strAlertMessage + strInstallOK, '', { dangerouslyUseHTMLString: true })
                  // document.body.innerHTML = strAlertMessage + strInstallOK + document.body.innerHTML;
                }
            }
        } else {
            //==Lodop,:==
            if (oOBJECT || oEMBED) {
                if (isWinIE)
                    LODOP = oOBJECT;
                else
                    LODOP = oEMBED;
            } else if (!CreatedOKLodopObject) {
                LODOP = document.createElement("object");
                LODOP.setAttribute("width", 0);
                LODOP.setAttribute("height", 0);
                LODOP.setAttribute("style", "position:absolute;left:0px;top:-100px;width:0px;height:0px;");
                if (isWinIE)
                    LODOP.setAttribute("classid", "clsid:2105C259-1E0C-4534-8141-A753534CB4CA");
                else
                    LODOP.setAttribute("type", "application/x-print-lodop");
                document.documentElement.appendChild(LODOP);
                CreatedOKLodopObject = LODOP;
            } else
                LODOP = CreatedOKLodopObject;
            //==Lodop:==
            if ((!LODOP) || (!LODOP.VERSION)) {
                MessageBox.alert((isWinIE64 ? strLodop64Install : strLodopInstall) + strInstallOK, '', { dangerouslyUseHTMLString: true })
                // document.body.innerHTML = (isWinIE64 ? strLodop64Install : strLodopInstall) + strInstallOK + document.body.innerHTML;
                return LODOP;
            }
            if (LODOP.VERSION < "6.2.2.6") {
                MessageBox.alert((isWinIE64 ? strLodop64Update : strLodopUpdate) + strInstallOK, '', { dangerouslyUseHTMLString: true })
                // document.body.innerHTML = (isWinIE64 ? strLodop64Update : strLodopUpdate) + strInstallOK + document.body.innerHTML;
            }
        }
        //===():=======================


        //===============================================================================
        return LODOP;
    } catch (err) {
        alert("getLodop:" + err);
    }
}
function lodopExample(ip) {
 return new Promise((resolve, reject) => {
  if (ip) {
    const dom = document.getElementById('lodop')
    if (dom) {
      dom.remove()
    }
    window.LoadJsState = ''
    window.getCLodop = null
    setJsUrl(ip)
    loadCLodop()
  }
  if (LoadJsState == "complete") {
    if(window.getCLodop) {
      resolve(getCLodop())
    } else {
      getLodop2()
      reject('WebCLodop')
    }
  } else {
    var counter = 0;
    var maxNum = 5000;
    function myFunc() {
      counter++;
      if (LoadJsState == "complete") {
        clearInterval(intervalId)
        if(window.getCLodop) {
          resolve(getCLodop())
        } else {
          getLodop2()
          reject('WebCLodop')
        }
      } else {
        if (counter == maxNum) {
          clearInterval(intervalId)
          getLodop2()
          reject('')
        }
      }
    }
    var intervalId = setInterval(myFunc, 20);
  }
 })
}

export default lodopExample
